<template>
  <div class="sharemain">
    <div class="goback animation-slide-top">
      <el-button type="primary" @click="goEdit()">返回</el-button>
    </div>
    <div class="bgc animation-slide-top"></div>
    <div class="thearticleinfo animation-slide-right">
      <div class="name">{{ this.modle.title }}</div>
      <div class="tags">
        <div
          v-for="(tags, i) in this.modle.tagList"
          :key="i"
          :index="i"
          class="tag"
        >
          {{ tags.tagName }}
        </div>
      </div>
      <div v-html="modle.text" class="body"></div>
      <div class="time">
        <i class="el-icon-date"></i>
        <p>{{ this.modle.time }}</p>
      </div>
    </div>
    <div class="discuss animation-slide-right">
      <el-input placeholder="请输入内容" v-model="input" clearable> </el-input>
      <el-button class="btn" type="primary" @click="discuss()"
        >发送评论</el-button
      >
    </div>
    <div class="tipsBox animation-slide-right">
      <div class="tips">评论：</div>
      <div class="tipsbg">评论：</div>
    </div>

    <div class="discussList animation-slide-right">
      <div class="thediscuss" v-for="(item, i) in list" :key="i" :index="i">
        <div class="text">{{ item.text }}</div>
        <div class="timeAndUser">{{ item.uid }} {{ item.time }}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    id: {},
  },
  data() {
    return {
      modle: {},
      input: "",
      list: [],
    };
  },
  methods: {
    async fetch() {
      const res = await this.$http.get(`/share/content?id=${this.id}`);
      this.modle = res.data;
      console.log(this.modle);
    },
    async fetchdiscuss() {
      const res = await this.$http.get(`/discuss/list?id=${this.id}`);
      this.list = res.data.reverse();
      console.log(this.list);
    },
    goEdit() {
      this.$router.push("/Forum");
    },
    async discuss() {
      const res = await this.$http.post("/discuss/create", {
        share_id: this.modle.id,
        content: this.input,
      });
      this.fetchdiscuss();
      this.input = "";
    },
  },

  created() {
    this.fetch();
    this.fetchdiscuss();
  },
};
</script>

<style lang="scss" scoped>
