  display: block;
  .tags {
    display: flex;
    position: relative;
    left: -5px;
    .tag {
      height: 25px;
      background-color: #49726c;
      margin: 0 5px;
      padding: 0 10px;
      line-height: 25px;
      border-radius: 10px;
      color: white;
      font-size: 0.5em;
    }
  }
  .name {
    font-size: 5rem;
    font-weight: bolder;
    position: relative;
    letter-spacing: 2px;
    padding-bottom: 3px;
    text-align: left;
    color: rgb(99, 99, 99);
  }
  .active {
    border-bottom: 2px solid rgb(99, 99, 99);
  }
  .body {
    position: relative;
    text-align: left;
    margin-top: 10px;
  }
  .time {
    height: 18px;
    position: relative;
    margin-top: 20px;
    margin-bottom: 40px;
    .el-icon-date {
      float: left;
      color: rgb(161, 161, 161);
    }
    p {
      float: left;
      margin-left: 10px;
      margin-top: 0;
      margin-bottom: 0;
      font-size: 0.8em;
      letter-spacing: 1px;
      color: rgb(161, 161, 161);
    }
  }
}

.goback {
  width: 100%;
  box-sizing: border-box;
  position: fixed;
  box-shadow: 3px 4px 4px rgb(211, 211, 211);
  padding: 15px;
  top: 0;
  z-index: 99;
  display: flex;
}
.bgc {
  width: 100%;
  height: 70px;
  box-sizing: border-box;
  position: fixed;
  box-shadow: 3px 4px 4px rgb(211, 211, 211);
  padding: 15px;
  background-color: #fff;
  top: 0;
  z-index: 9;
  display: flex;
}
.goback::after {
  height: 100%;
  border-radius: 20px;
  content: "";
  background-image: url(../assets/bgi.jpg);
  opacity: 0.1;
  top: 0;
