    .theshare {
      height: 50px;
    }
    .text {
      color: #333;
      font-family: "shoujin";
      font-weight: bolder;
      font-size: 1rem;
      display: block;
      text-align: left;
      position: relative;
      margin-left: 20px;
      margin-top: 10px;
    }
    .timeAndUser {
      color: rgb(172, 172, 172);
      font-family: "shoujin";
      font-weight: bolder;
      font-size: 0.7rem;
      margin-left: 20px;

      margin-top: 10px;
      margin-bottom: 10px;
      text-align: left;
      display: block;
      position: relative;
    }
  }
}
.tipsBox {
  position: relative;
  top: 30px;

  height: 40px;
}
.tips {
  position: absolute;
