<template>
  <div class="forumMain">
    <div class="new animation-slide-top">
      <el-button type="primary" @click="goEdit()">发表看法</el-button>
    </div>
    <div class="bgc animation-slide-top"></div>
    <div class="shareList">
      <router-link
        tag="div"
        class="nav-link"
        v-for="(item, i) in shareList"
        :to="`/Share/${item.id}`"
        :key="i"
        :index="i"
      >
        <div class="share animation-slide-right">
          <div class="title">{{ item.title }}</div>
          <div class="timeAndUser">{{ item.uid }} {{ item.time }}</div>
        </div>
      </router-link>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      shareList: [],
    };
  },
  mounted() {
    this.getShareList();
  },
  methods: {
    async getShareList() {
      const res = await this.$http.get("/share/list");
      this.shareList = res.data.reverse();
    },
    goEdit() {
      this.$router.push("/Edit");
    },
  },
};
</script>

<style lang="scss" scoped>
.forumMain {
  .new {
    width: 100%;
    box-sizing: border-box;
    position: relative;
    box-shadow: 3px 4px 4px rgb(211, 211, 211);
    padding: 15px;
    position: fixed;
    display: flex;
    z-index: 99;
  }
  .bgc {
    width: 100%;
    height: 70px;
    box-sizing: border-box;
    position: fixed;
    box-shadow: 3px 4px 4px rgb(211, 211, 211);
    padding: 15px;
    background-color: #fff;
    top: 0;
    z-index: 9;
    display: flex;
  }
  .new::after {
    height: 100%;
    border-radius: 20px;
    content: "";
    background-image: url(../assets/bgi.jpg);
    opacity: 0.1;
    top: 0;
    background-size: 30%;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
  }
  .shareList {
    position: relative;
    top: 80px;
    .share {
      z-index: -1;
      box-shadow: 7px 3px 4px rgb(240, 240, 240);
      position: relative;
      height: 80px;
      .theshare {
        height: 80px;
      }
      .title {
        color: #c71b42;
        font-family: "shoujin";
        font-weight: bolder;
        font-size: 2.5rem;
        display: block;
        position: absolute;
        left: 20px;
        top: 10px;
      }
      .timeAndUser {
        color: #333;
        font-family: "shoujin";
        font-weight: bolder;
        font-size: 0.9rem;
        display: block;
        position: absolute;
        left: 20px;
        bottom: 10px;
      }
    }
  }
}
</style>