<template>
  <div class="shareList">
    <router-link
      tag="div"
      class="nav-link"
      v-for="(item, i) in shareList"
      :to="`/Share/${item.id}`"
      :key="i"
      :index="i"
    >
      <div class="share animation-slide-right">
        <div class="title">{{ item.title }}</div>
        <div class="timeAndUser">{{ item.uid }} {{ item.time }}</div>
      </div>
    </router-link>
  </div>
</template>

<script>
export default {
  data() {
    return {
      shareList: [],
    };
  },
  mounted() {
    this.getShareList();
  },
  methods: {
    async getShareList() {
      const res = await this.$http.get("/share/getMyshare");
      this.shareList = res.data.reverse();
    },
    goEdit() {
      this.$router.push("/Edit");
    },
  },
};
</script>

<style lang="scss" scoped>
.shareList {
  position: relative;
  .share {
    z-index: -1;
    box-shadow: 7px 3px 4px rgb(240, 240, 240);
    position: relative;
    height: 80px;
    .theshare {
      height: 80px;
    }
    .title {
      color: #c71b42;
      font-family: "shoujin";
      font-weight: bolder;
      font-size: 2.5rem;
      display: block;
      position: absolute;
      left: 20px;
      top: 10px;
    }
    .timeAndUser {
      color: #333;
      font-family: "shoujin";
      font-weight: bolder;
      font-size: 0.9rem;
      display: block;
      position: absolute;
      left: 20px;
      bottom: 10px;
    }
  }
}
</style>