<template>
  <div class="forumMain">
    <div class="goback animation-slide-top">
      <el-button type="primary" @click="goEdit()">返回</el-button>
    </div>
    <div class="bgc animation-slide-top"></div>
    <div class="content">
      <el-form @submit.native.prevent="save" label-width="120px">
        <el-form-item label="标题">
          <el-input v-model="model.title" placeholder="请输入内容"></el-input>
        </el-form-item>
        <el-form-item label="正文">
          <vue-editor v-model="model.blogContent" class="input"></vue-editor>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" native-type="submit">保存</el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import { VueEditor } from "vue2-editor";

export default {
  components: {
    VueEditor,
  },
  data() {
    return {
      model: {},
    };
  },
  mounted() {},
  methods: {
    goEdit() {
      this.$router.push("/Forum");
    },
    async save() {
      await this.$http.post("/share/create", { share: this.model });
      this.$router.push("/Forum");
      this.$message({
        type: "success",
        message: "保存成功",
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.forumMain {
  .goback {
    width: 100%;
    box-sizing: border-box;
    position: fixed;
    box-shadow: 3px 4px 4px rgb(211, 211, 211);
    padding: 15px;
    top: 0;
    z-index: 99;
    display: flex;
  }
  .bgc {
    width: 100%;
    height: 70px;
    box-sizing: border-box;
    position: fixed;
    box-shadow: 3px 4px 4px rgb(211, 211, 211);
    padding: 15px;
    background-color: #fff;
    top: 0;
    z-index: 9;
    display: flex;
  }
  .goback::after {
    height: 100%;
    border-radius: 20px;
    content: "";
    background-image: url(../assets/bgi.jpg);
    opacity: 0.1;
    top: 0;
    background-size: 30%;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
  }
}
.content {
  position: relative;
  margin: 0 auto;
  width: 80%;
  margin-top: 100px !important;
}
</style>