  width: 100vw;
  height: 100vh;
  position: absolute;
  background-color: #ffffff;
  overflow: hidden;
  z-index: 99;
  .slogan {
    font-family: "shoujin";
    text-align: center;
    line-height: 100vh;
    font-size: 5vw;
