  }
  .Mobius {
    width: 100%;
  }
  .trueLogoBox {
    margin: 0 auto;
    position: fixed;
    top: 10px;
    left: 0;
    box-sizing: border-box;
    right: 0;
    animation: login 0.7s 1.9s, rotate 1s 2.6s;
    animation-fill-mode: forwards;
    z-index: 9;
    border: 3px solid rgba(51, 51, 51, 0);
    border-radius: 10px;
    pointer-events: none;
  }
  /*
  @keyframes bordershow {
    0% {
      border: 3px solid rgba(51, 51, 51, 0.1);
      border-radius: 10px;
    }
    10% {
      border: 3px solid rgba(51, 51, 51, 0.15);
      border-radius: 10px;
    }
    20% {
      border: 3px solid rgba(51, 51, 51, 0.2);
      border-radius: 10px;
    }
    30% {
      border: 3px solid rgba(51, 51, 51, 0.3);
      border-radius: 10px;
    }
    40% {
      border: 3px solid rgba(51, 51, 51, 0.4);
      border-radius: 10px;
    }
    50% {
      border: 3px solid rgba(51, 51, 51, 0.5);
      border-radius: 10px;
    }
    60% {
      border: 3px solid rgba(51, 51, 51, 0.6);
      border-radius: 10px;
    }
    70% {
      border: 3px solid rgba(51, 51, 51, 0.7);
      border-radius: 10px;
    }
    80% {
      border: 3px solid rgba(51, 51, 51, 0.8);
      border-radius: 10px;
    }
    90% {
      border: 3px solid rgba(51, 51, 51, 0.9);
      border-radius: 10px;
    }
    100% {
      border: 3px solid rgb(51, 51, 51);
      border-radius: 10px;
    }
  }*/
  @keyframes login {
    0% {
      top: 10px;
    }
    30% {
      top: 300px;
      opacity: 1;
    }
    60% {
      top: 275px;
    }
    75% {
      top: 300px;
    }
    87.5% {
      top: 295px;
    }
    100% {
      top: 300px;
    }
  }
  @keyframes rotate {
    0% {
      transform: perspective(500px) rotateX(0deg);
      top: 300px;
    }
    50% {
      transform: perspective(500px) rotateX(30deg) scale(1.3);
      top: 260px;
    }
    100% {
      transform: perspective(500px) rotateX(0deg) scale(1);
      top: 110px;
    }
  }
}
.login {
  width: 23%;
  height: 250px;
  left: 38.5%;
  top: 130px;
  padding-top: 120px;
  text-align: center;
  border-radius: 5px;
  position: absolute;
  background-color: #fdfaf3;
  box-shadow: 5px 5px 20px rgb(197, 197, 197);
  animation: loginin 1s 3.1s;
  animation-fill-mode: forwards;
  opacity: 0;
}
.input {
  z-index: 999;
  width: 250px;
  height: 30px;
  outline: none;
  border-radius: 10px;
  border: none;
  margin-bottom: 20px;
  padding-left: 5px;
}
@keyframes loginin {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
.btn {
  border: none;
  margin-right: 25px;
  margin-left: 25px;
  background-color: rgb(255, 255, 255);
  outline: none;
  height: 30px;
  width: 25%;
  border-radius: 5px;
  overflow: hidden;
  top: 40px;
  position: relative;
  transition: all 0.5s ease;
  box-shadow: 2px 2px 2px rgb(211, 211, 211);
}
.btn:hover {
  transform: translateY(-4px);
  box-shadow: 4px 4px 10px rgb(126, 126, 126);
}
@keyframes in {
  0% {
    top: -1000px;
    opacity: 0;
  }
  50% {
    top: 0px;
    opacity: 1;
  }
  62.5% {
    top: -25px;
  }
  75% {
    top: 0px;
  }
  87.5% {
    top: -5px;
  }
  100% {
    top: 0px;
  }
}
.btntext {
  z-index: 100;
  position: absolute;
  line-height: 30px;
  top: 0;
  width: 100%;
