<template>
  <div class="chartMain">
    <div
      ref="mapbox"
      class="mapBox animation-slide-right"
      style="width: 600px; height: 500px; margin: 0 auto"
    ></div>
    <div class="functiochoose">
      <el-radio-group v-model="radio1" @change="change">
        <el-radio-button v-model="radio1" label="1" border
          >现存确诊</el-radio-button
        >
        <el-radio-button v-model="radio1" label="2" border
          >历史确诊</el-radio-button
        >
        <el-radio-button v-model="radio1" label="3" border
          >历史治愈</el-radio-button
        >
      </el-radio-group>
    </div>
    <div
      id="echartsWordcloud"
      class="cloudBox"
      style="width: 600px; height: 500px; margin: 0 auto"
    ></div>
  </div>
</template>

<script>
import echarts from "echarts";
require("echarts-wordcloud");
import "echarts/map/js/china.js";
import jsonp from "jsonp";
var option = {
  title: {
    // 标题内容
    text: "全国疫情现存确诊地图",
  },
  series: [
    {
      name: "现存确诊",
      type: "map",
      // 告诉echarts渲染一个地图
      map: "china",
      // 告诉echarts渲染中国地图
      label: {
        // 设置地区汉字
        show: true,
        color: "#333",
        fontSize: 10,
      },
      itemStyle: {
        // 地图区域样式
        areaColor: "#eee",
      },
      roam: true,
      // 鼠标滚轮效果
      zoom: 1.2,
      // 地图放大缩小
      emphasis: {
        // 鼠标经过地图和字体样式
        label: {
          color: "#fff",
          fontSize: 12,
        },
        itemStyle: {
          areaColor: "#ccc",
        },
      },
      data: [],
      // 展示后台给的数据，必须格式（name:xxx;value:xxx）
    },
  ],
  visualMap: [
    {
      // 区域显示颜色
      type: "piecewise",
      show: true,
      // splitNumber: 4
      pieces: [
        { min: 500 },
        { min: 300, max: 399 },
        { min: 200, max: 299 },
        { min: 100, max: 199 },
        { min: 2, max: 99 },
        { min: 0, max: 1 },
      ],
      inRange: {
        // 区域图标样式
        symbol: "rect",
        color: ["#fff", "#FFAA85", "#FF7B69", "#CC2929", "#8C0D0D", "#660208"],
      },
    },
  ],
  tooltip: {
    // 鼠标放上去显示内容
    trigger: "item",
  },
  toolbox: {
    // 下载
    show: true,
    orient: "vertical",
    left: "right",
    top: "center",
    feature: {
      dataView: { readyOnly: false },
      restore: {},
      saveAsImage: {},
    },
  },
};

export default {
  name: "HelloWorld",
  data() {
    return {
      radio1: "1",
      provincedata: [],
      citydata: [],
      flag: "province",
    };
  },
  mounted() {
    this.getProvince();
    this.getCity();

    this.mycharts = echarts.init(this.$refs.mapbox);
    // 初始化echarts
    this.mycharts.setOption(option);
    var that = this;
    this.mycharts.on("click", function (params) {
      const flag = that.flag;
      console.log(flag);
      if (flag === "province") {
        that.flag = "city";
        if (params.componentType === "series") {
          const citydata = that.citydata;
          var chooseCitysData = [];
          for (let i in citydata) {
            if (citydata[i].prov_id == params.dataIndex + 1) {
              let value;
              if (that.radio1 == "1") {
                value = citydata[i].nowCon;
              } else if (that.radio1 == "2") {
                value = citydata[i].con;
              } else if (that.radio1 == "3") {
                value = citydata[i].heal;
              }
              chooseCitysData.push({
                value: value,

                name: citydata[i].name,
              });
            }
          }
          console.log(chooseCitysData);
          option = {};
          option = {
            tooltip: {
              trigger: "item",
              showDelay: 20,
              hideDelay: 20,
              backgroundColor: "rgba(60,63,65,0.2)",
              textStyle: {
                fontSize: "16px",
                color: "#000",
              },
            },
            title: {
              text: params.data.name + "疫情统计",
            },
            series: [
              {
                name: "",
                type: "pie",
                radius: ["30%", "60%"],
                data: chooseCitysData,
              },
            ],
          };
          that.mycharts.setOption(option);
        }
      } else if (flag === "city") {
        that.flag = "province";
        that.radio1 = "1";
        that.change(1);
      }
    });
  },
  created() {
    this.change(1);
  },
  methods: {
    change(index) {
      this.flag = "province";
      option = {};
      if (index == 1) {
        option = {
          title: {
            // 标题内容
            text: "全国疫情现存确诊地图",
          },
          series: [
            {
              name: "现存确诊",
              type: "map",
              // 告诉echarts渲染一个地图
              map: "china",
              // 告诉echarts渲染中国地图
              label: {
                // 设置地区汉字
                show: true,
                color: "#333",
                fontSize: 10,
              },
              itemStyle: {
                // 地图区域样式
                areaColor: "#eee",
              },
              roam: true,
              // 鼠标滚轮效果
              zoom: 1.2,
              // 地图放大缩小
              emphasis: {
                // 鼠标经过地图和字体样式
                label: {
                  color: "#fff",
                  fontSize: 12,
                },
                itemStyle: {
                  areaColor: "#ccc",
                },
              },
              data: [],
              // 展示后台给的数据，必须格式（name:xxx;value:xxx）
            },
          ],
          visualMap: [
            {
              // 区域显示颜色
              type: "piecewise",
              show: true,
              // splitNumber: 4
              pieces: [
                { min: 500 },
                { min: 300, max: 499 },
                { min: 200, max: 299 },
                { min: 100, max: 199 },
                { min: 2, max: 99 },
                { min: 0, max: 1 },
              ],
              inRange: {
                // 区域图标样式
                symbol: "rect",
                color: [
                  "#fff",
                  "#FFAA85",
                  "#FF7B69",
                  "#CC2929",
                  "#8C0D0D",
                  "#660208",
                ],
              },
            },
          ],
          tooltip: {
            // 鼠标放上去显示内容
            trigger: "item",
          },
          toolbox: {
            // 下载
            show: true,
            orient: "vertical",
            left: "right",
            top: "center",
            feature: {
              dataView: { readyOnly: false },
              restore: {},
              saveAsImage: {},
            },
          },
        };
      } else if (index == 2) {
        option = {
          title: {
            // 标题内容
            text: "全国疫情历史确诊地图",
          },
          series: [
            {
              name: "历史确诊人数",
              type: "map",
              // 告诉echarts渲染一个地图
              map: "china",
              // 告诉echarts渲染中国地图
              label: {
                // 设置地区汉字
                show: true,
                color: "#333",
                fontSize: 10,
              },
              itemStyle: {
                // 地图区域样式
                areaColor: "#eee",
              },
              roam: true,
              // 鼠标滚轮效果
              zoom: 1.2,
              // 地图放大缩小
              emphasis: {
                // 鼠标经过地图和字体样式
                label: {
                  color: "#fff",
                  fontSize: 12,
                },
                itemStyle: {
                  areaColor: "#ccc",
                },
              },
              data: [],
              // 展示后台给的数据，必须格式（name:xxx;value:xxx）
            },
          ],
          visualMap: [
            {
              // 区域显示颜色
              type: "piecewise",
              show: true,
              // splitNumber: 4
              pieces: [
                { min: 10000 },
                { min: 1000, max: 9999 },
                { min: 100, max: 999 },
                { min: 10, max: 99 },
                { min: 1, max: 9 },
                { max: 0 },
              ],
              inRange: {
                // 区域图标样式
                symbol: "rect",
                color: [
                  "#FFFFFF",
                  "#FFAA85",
                  "#FF7B69",
                  "#CC2929",
                  "#8C0D0D",
                  "#660208",
                ],
              },
            },
          ],
          tooltip: {
            // 鼠标放上去显示内容
            trigger: "item",
          },
          toolbox: {
            // 下载
            show: true,
            orient: "vertical",
            left: "right",
            top: "center",
            feature: {
              dataView: { readyOnly: false },
              restore: {},
              saveAsImage: {},
            },
          },
        };
      } else if (index == 3) {
        option = {
          title: {
            // 标题内容
            text: "全国疫情历史治愈地图",
          },
          series: [
            {
              name: "历史治愈人数",
              type: "map",
              // 告诉echarts渲染一个地图
              map: "china",
              // 告诉echarts渲染中国地图
              label: {
                // 设置地区汉字
                show: true,
                color: "#333",
                fontSize: 10,
              },
              itemStyle: {
                // 地图区域样式
                areaColor: "#eee",
              },
              roam: true,
              // 鼠标滚轮效果
              zoom: 1.2,
              // 地图放大缩小
              emphasis: {
                // 鼠标经过地图和字体样式
                label: {
                  color: "#fff",
                  fontSize: 12,
                },
                itemStyle: {
                  areaColor: "#ccc",
                },
              },
              data: [],
              // 展示后台给的数据，必须格式（name:xxx;value:xxx）
            },
          ],
          visualMap: [
            {
              // 区域显示颜色
              type: "piecewise",
              show: true,
              // splitNumber: 4
              pieces: [
                { min: 10000 },
                { min: 1000, max: 9999 },
                { min: 100, max: 999 },
                { min: 10, max: 99 },
                { min: 1, max: 9 },
                { max: 0 },
              ],
              inRange: {
                // 区域图标样式
                symbol: "rect",
                color: [
                  "#fff",
                  "#00FF66",
                  "#00CC66",
                  "#00CC00",
                  "#009933",
                  "#006600",
                ],
              },
            },
          ],
          tooltip: {
            // 鼠标放上去显示内容
            trigger: "item",
          },
          toolbox: {
            // 下载
            show: true,
            orient: "vertical",
            left: "right",
            top: "center",
            feature: {
              dataView: { readyOnly: false },
              restore: {},
              saveAsImage: {},
            },
          },
        };
      }
      this.getData();
      this.mycharts = echarts.init(this.$refs.mapbox);
      this.mycharts.setOption(option);
    },
    async getProvince() {
      const res = await this.$http.get("/province/all");
      this.provincedata = res.data;
      this.getData();
      this.initEcharts();
    },
    async getCity() {
      const res = await this.$http.get("/city/all");
      this.citydata = res.data;
      console.log(this.citydata);
    },
    getData() {
      let list = [];
      let item;
      const provincedata = this.provincedata;
      for (item in provincedata) {
        let value;
        if (this.radio1 == 1) {
          value = provincedata[item].nowCon;
        } else if (this.radio1 == 2) {
          value = provincedata[item].con;
        } else if (this.radio1 == 3) {
          value = provincedata[item].heal;
        }
        list.push({
          name: provincedata[item].name,
          value: value,
        });
      }
      console.log(list);
      option.series[0].data = list;
      this.mycharts.setOption(option);
      var that = this;
    },
    initEcharts() {
      this.echartsWordcloud = this.$echarts.init(
        document.getElementById("echartsWordcloud")
      );
      let datas = [];
      const provincedata = this.provincedata;
      console.log(provincedata);
      for (let i in provincedata) {
        let item = {};
        item.value = provincedata[i].con;
        item.name = provincedata[i].name;
        console.log(item);
        datas.push(item);
      }
      console.log(datas);
      let option1 = {
        title: {
          text: "",
          x: "center",
        },
        series: [
          {
            type: "wordCloud",
            //用来调整词之间的距离
            gridSize: 10,
            //用来调整字的大小范围
            sizeRange: [14, 100],
            rotationRange: [0, 0],
            //随机生成字体颜色
            textStyle: {
              normal: {
                color: function () {
                  return (
                    "rgb(" +
                    Math.round(Math.random() * 255) +
                    ", " +
                    Math.round(Math.random() * 255) +
                    ", " +
                    Math.round(Math.random() * 255) +
                    ")"
                  );
                },
              },
            },
            //位置相关设置
            left: "center",
            top: "center",
            right: null,
            bottom: null,
            width: "1000%",
            height: "1000%",
            //数据
            data: datas,
          },
        ],
      };
      // console.log(option1);
      this.echartsWordcloud.setOption(option1);
    },
  },
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
h1,
h2 {
  font-weight: normal;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.chartMain {
  height: fit-content;
  position: relative;
  .mapBox {
    position: relative;
    margin-top: 80px !important;
    border-radius: 20px;
    box-shadow: 5px 5px 20px rgb(197, 197, 197);
    background-color: #fdfaf325;
    padding: 20px;
    transition: 1s;
  }
  .mapBox::after {
    border-radius: 20px;
    content: "";
    background-image: url(../assets/bgi.jpg);
    opacity: 0.1;
    top: 0;
    background-size: cover;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
  }
  .cloudBox {
    position: relative;
    margin-top: 100px !important;
    margin-bottom: 100px !important;
    border-radius: 20px;
    box-shadow: 5px 5px 20px rgb(197, 197, 197);
    background-color: #fdfaf325;
    padding: 20px;
    transition: 1s;
  }
  .cloudBox::after {
    border-radius: 20px;
    content: "";
    background-image: url(../assets/bgi.jpg);
    opacity: 0.1;
    top: 0;
    background-size: cover;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    z-index: -1;
  }
  .functiochoose {
    position: relative;
    margin-top: 40px;
  }
}
</style>